body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(31, 32, 32);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container{
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.header{
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background-color: rgba(0, 255, 255, 0.384);

}

.router{
  text-align: center;
  display: flex;
  justify-content: center; 
  width: 100%;
  font-size: 27px;
  gap: 50px;
  
}

a{
  text-decoration: none;
  color:rgb(161, 161, 161);
  padding: 10px;
}

a:hover{
  color:rgb(161, 161, 161);
  padding: 10px;
  background-color: rgba(255, 235, 205, 0.281);
  color:rgb(44, 44, 44);

}
.header >h1 {
  color: white;
}


.level1{
  color: white;
}
.level2{
  color: white;
}

button{
  padding:  15px;
  background-color: #54a0ff;
  border: none;
  border-radius: 10px;
  color: white;
  cursor: pointer;
  transition: 0.3s;
  font-size: 20px;
  margin-top: 150px;
}

button:hover{
transform: scale(1.2);
}